@font-face {
  font-family: 'Gotham';
  src: url('https://cgcdn.s3.amazonaws.com/global-ui/fonts/Gotham-Book.eot');
  src: url('https://cgcdn.s3.amazonaws.com/global-ui/fonts/Gotham-Book.eot?#iefix')
      format('embedded-opentype'),
    url('https://cgcdn.s3.amazonaws.com/global-ui/fonts/Gotham-Book.woff')
      format('woff'),
    url('https://cgcdn.s3.amazonaws.com/global-ui/fonts/Gotham-Book.ttf')
      format('truetype'),
    url('https://cgcdn.s3.amazonaws.com/global-ui/fonts/Gotham-Book.svg')
      format('svg');
  font-style: normal;
  font-weight: 300;
}

